<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card id="params-loading-container" class="mb-base vs-con-loading__container">
      <h4 class="mb-base"><i :class="[report.iconClass, 'mr-1']" v-if="report.iconClass"></i> {{ report.reportName }}</h4>

      <div class="code-editor p-4 mb-base" v-if="report.runNotes">
        <markdown-it-vue :content="report.runNotes"/>
      </div>

      <div v-if="price.priceId && !priceAccepted">
        <div class="nv-row">
          <div class="nv-col w-full">
            <vs-alert active="true" color="warning" class="mb-4">
              <i class="far fa-exclamation-circle pr-1"></i> {{ $t('REPORT_INCURS_CHARGE') }} {{ price.price }} {{ price.currencyAbbreviation }}.
            </vs-alert>
            <div class="flex justify-between">
              <vs-button class="px-5 py-3" color="primary" type="border" @click="cancel">{{ $t('CANCEL') }}</vs-button>
              <vs-button class="px-5 py-3" color="primary" type="filled" @click="priceAccepted = true">{{ $t('ACCEPT') }}</vs-button>
            </div>
          </div>
        </div>
      </div>

      <template v-if="price.priceId && priceAccepted">
        <h5 class="mb-2">{{ $t('CHARGES') }}</h5>
        <div class="mb-base">
          <div class="nv-row mb-4">
            <div class="nv-col w-full">
              <label class="vs-input--label">{{ $t('DEBIT_ACCOUNT') }}</label>
              <account-lookup :title="$t('DEBIT_ACCOUNT')" popupRef="debitAccountPopup" valueField="accountNumber" displayField="accountNumber" @select-record="selectDebitAccountRecord"/>
            </div>
          </div>
        </div>
      </template>

      <template v-if="priceAccepted">
        <h5 class="mb-2">{{ $t('PARAMETERS') }}</h5>
        <div class="mb-base">
          <div v-if="inputParameterList.length === 0">
            <vs-alert active="true" color="primary">
              <i class="far fa-check-circle pr-1"></i> {{ $t('NO_PARAMETERS') }}
            </vs-alert>
          </div>
          <template v-for="(parameter, index) in inputParameterList">
            <div class="nv-row mb-4" v-if="parameter.parameterTypeEnumId !== 'RPT_PARAM_TYPE_CONTEXT'" :key="index">
              <div class="nv-col w-full" :class="parameter.mandatory === 'Y' ? 'required' : ''">
                <label class="vs-input--label">{{ parameter.displayName }}</label>
                <template v-if="parameter.parameterTypeEnumId === 'RPT_PARAM_TYPE_ACCT'">
                  <account-lookup :title="parameter.displayName" :lookupTag="index" valueField="accountNumber" displayField="accountNumber" @select-record="selectAccountRecord"/>
                </template>
                <template v-else-if="parameter.parameterTypeEnumId === 'RPT_PARAM_TYPE_NUMBER'">
                  <vs-input type="number" class="w-full" v-model="parameter.parameterValue"/>
                </template>
                <template v-else-if="parameter.parameterTypeEnumId === 'RPT_PARAM_TYPE_DATE'">
                  <nv-input-group>
                    <template slot="append">
                      <div class="append-text bg-dark">
                        <i class="far fa-calendar-alt"></i>
                      </div>
                    </template>
                    <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="parameter.parameterValue" :format="dateFormat"></datepicker>
                  </nv-input-group>
                </template>
                <template v-else>
                  <vs-input class="w-full" v-model="parameter.parameterValue"/>
                </template>
              </div>
            </div>
          </template>
        </div>

        <div class="nv-row">
          <div class="nv-col w-full">
            <div class="flex justify-between">
              <vs-button class="px-5 py-3" color="primary" type="border" @click="cancel">{{ $t('CANCEL') }}</vs-button>
              <vs-button class="px-5 py-3" color="primary" type="filled" @click="run" :disabled="resultTableBusy">{{ $t('SUBMIT') }}</vs-button>
            </div>
          </div>
        </div>
      </template>
    </nv-card>

    <template v-if="resultTableVisible">
      <nv-card id="result-loading-container" class="vs-con-loading__container">
        <vs-table :data="resultTableData" :noDataText="resultTableNoDataText" search>
          <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
            <div class="flex flex-wrap-reverse items-center">
              <vs-button class="p-3 mr-4" @click="exportXls(resultTableData, columnDefs)" type="filled" icon="fa-file-excel" icon-pack="fal">{{ $t('EXPORT_XLS') }}</vs-button>
              <vs-button class="p-3 mr-4" @click="exportCsv(resultTableData, columnDefs)" type="filled" icon="fa-file-csv" icon-pack="fal">{{ $t('EXPORT_CSV') }}</vs-button>
            </div>
            <div class="flex justify-end mr-4">
              <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
                <span class="mx-2">{{ resultTablePagingInfo }}</span>
              </div>
            </div>
          </div>

          <template slot="thead">
            <vs-th :sort-key="parameter.parameterName" v-for="(parameter, thIndex) in outputParameterList" :key="thIndex">
              {{ parameter.displayName }}
            </vs-th>
          </template>

          <template slot-scope="{data}">
            <vs-tr v-for="(tr, index) in data" :data="tr" :key="index">
              <vs-td v-for="(parameter, paramIndex) in outputParameterList" :key="paramIndex">
                <template v-if="parameter.parameterTypeEnumId === 'RPT_PARAM_TYPE_NUMBER'">
                  {{ data[index][parameter.parameterName] | currency('') | placeholder('-') }}
                </template>
                <template v-else-if="parameter.parameterTypeEnumId === 'RPT_PARAM_TYPE_DATE'">
                  {{ data[index][parameter.parameterName] | formatUnixDate | placeholder('-') }}
                </template>
                <template v-else>
                  {{ data[index][parameter.parameterName] | placeholder('-') }}
                </template>
              </vs-td>
            </vs-tr>
          </template>
        </vs-table>
      </nv-card>
    </template>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import DatePickerMixin from '@/mixins/commons/DatePickerMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import AccountLookup from '@/views/account/account-root/AccountLookup'
import ReportService from '@/services/report/ReportService'
import Datepicker from 'vuejs-datepicker'
import MarkdownItVue from 'markdown-it-vue'

export default {
  name: 'report-run',
  mixins: [AlertMixin, DatePickerMixin, ListLayoutMixin, LoadingMixin],
  components: {
    Datepicker,
    AccountLookup,
    MarkdownItVue
  },
  data: function () {
    return {
      reportId: this.$route.params.id,
      report: {},
      price: {},
      priceAccepted: false,
      debitAccountNumber: null,
      inputParameterList: [],
      outputParameterList: [],
      resultTableData: [],
      resultTableContainer: 'result-loading-container',
      resultTableBusy: false,
      resultTableTotalRows: 0,
      resultTableCurrentPage: 1,
      resultTablePerPage: 10,
      resultTableVisible: false
    }
  },
  computed: {
    resultTableNoDataText: function () {
      return this.resultTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    resultTablePagingInfo: function () {
      return this.pagingInfo(this.resultTableCurrentPage, this.resultTablePerPage, this.resultTableTotalRows)
    },
    columnDefs: function () {
      let self = this
      let fields = []
      for (let i = 0; i < this.outputParameterList.length; i++) {
        let fieldObj = {
          headerName: self.outputParameterList[i].displayName,
          field: self.outputParameterList[i].parameterName
        }
        if (this.outputParameterList[i].parameterTypeEnumId === 'RPT_PARAM_TYPE_DATE') {
          fieldObj.format = 'Date'
        } else if (this.outputParameterList[i].parameterTypeEnumId === 'RPT_PARAM_TYPE_NUMBER') {
          fieldObj.format = 'Number'
        } else if (this.outputParameterList[i].parameterTypeEnumId === 'RPT_PARAM_TYPE_DECIMAL') {
          fieldObj.format = 'Decimal'
        }
        fields.push(fieldObj)
      }
      return fields
    }
  },
  methods: {
    async load () {
      let self = this
      self.startLoading('params-loading-container')
      ReportService.reportApps({
        reportId: this.reportId
      }).then(function (response) {
        self.report = response.data.reportAppList[0] || {}
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })

      ReportService.reportParameters({
        reportId: this.reportId,
        parameterDirectionEnumId: 'RPT_PARAM_DIR_IN',
        disabled: 'N',
        pageNoLimit: true
      }).then(function (response) {
        self.inputParameterList = response.data.reportParameterList || []
      })

      ReportService.reportPrice({
        reportId: this.reportId
      }).then(function (response) {
        self.price = response.data || {}
        self.priceAccepted = self.price.priceId ? self.price.price === 0 : true
      })

      ReportService.reportParameters({
        reportId: this.reportId,
        parameterDirectionEnumId: 'RPT_PARAM_DIR_OUT',
        disabled: 'N',
        pageNoLimit: true
      }).then(function (response) {
        self.outputParameterList = response.data.reportParameterList || []
        self.stopLoading('params-loading-container')
      })
    },
    selectDebitAccountRecord (record) {
      this.debitAccountNumber = record.accountNumber
    },
    selectAccountRecord (record, lookupTag) {
      this.inputParameterList[lookupTag].parameterValue = record.accountNumber
    },
    run () {
      let parameters = []
      for (let i = 0; i < this.inputParameterList.length; i++) {
        parameters.push({
          parameterId: this.inputParameterList[i].parameterId,
          parameterValue: this.inputParameterList[i].parameterValue
        })
      }

      let self = this
      this.resultTableBusy = true
      self.startLoading('params-loading-container')
      this.startLoading(this.resultTableContainer)
      ReportService.runReport({
        reportId: this.reportId,
        debitAccountNumber: this.debitAccountNumber,
        parameters: JSON.stringify(parameters)
      }).then(function (response) {
        self.stopLoading('params-loading-container')
        self.stopLoading(self.resultTableContainer)
        self.resultTableBusy = false
        if (response.data.inlineDataList) {
          self.resultTableData = response.data.inlineDataList || []
          self.resultTableTotalRows = self.resultTableData.length
          self.resultTablePerPage = self.resultTableData.length
          self.resultTableVisible = true
        } else if (response.data.instanceId) {
          self.showSuccessAlert(response.data.messages)
        }
      }).catch(function (error) {
        self.stopLoading('params-loading-container')
        self.stopLoading(self.resultTableContainer)
        self.resultTableBusy = false
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
  }
}
</script>
